import React, { useState, useEffect } from 'react';
import Header from './Header';
import Note from './Note';
import CreateArea from './CreateArea';
import '../styles/App.scss';

function App() {

  const [note, setNote] = useState([]);

  useEffect(() => {
    const localNotes = localStorage.getItem("notes");
    if (localNotes) {
      setNote(JSON.parse(localNotes));
    }
  }, []);

  function addNote(input, setInput) {

    setNote(prevNotes => {
      return [...prevNotes, input]
    });

    setInput({ title: "", content: "" });
  }

  useEffect(() => {
    localStorage.setItem("notes", JSON.stringify(note))
  }, [note])

  function deleteNote(id) {
    setNote(prevNotes => {
      return prevNotes.filter((eachNote, index) => index !== id);
    });
  }

  return (
    <>
      <Header />
      <CreateArea onAdd={addNote} />
      {note.map((eachNote, index) => <Note key={index} id={index} title={eachNote.title} content={eachNote.content} onDelete={deleteNote} />)}
    </>
  );
}

export default App;
