import React from "react";
import { ThemeProvider, createTheme } from '@mui/material/styles';
import { Card, CardContent, Typography } from "@mui/material";
import { Delete } from "@mui/icons-material";

function Note({ title, content, id, onDelete }) {

    const darkTheme = createTheme({
        palette: {
            primary: {
                main: "#172543"
            }
        }
    })

    return (
        <ThemeProvider theme={darkTheme}>
            <Card variant="contained" className="card" sx={{ backgroundColor: "primary.main" }}>
                <CardContent>
                    <Typography variant="h1" component="h1" gutterBottom sx={{ color: "grey.100", font: "500 1.1em Poppins" }}>
                        {title}
                    </Typography>
                    <Typography variant="body1" component="p" gutterBottom sx={{ color: "grey.200", whiteSpace: "pre-wrap", wordBreak: "break-word", fontFamily: "DM Sans" }} >
                        {content}
                    </Typography>
                    <button className="delete-btn" onClick={() => onDelete(id)}>
                        <Delete sx={{ color: "grey.300" }} fontSize="small" />
                    </button>
                </CardContent>
            </Card>
        </ThemeProvider>
    );
}

export default Note;