import React, { useState } from "react";
import { Fab, Zoom } from '@mui/material';
import AddIcon from '@mui/icons-material/Add';
import "../styles/CreateArea.scss";

function CreateArea({ onAdd }) {
    const [input, setInput] = useState({
        title: "",
        content: ""
    });

    const [isExpanded, setExpanded] = useState(false);

    function handleChange(event) {
        const { name, value } = event.target;

        setInput(prevInput => {
            return {
                ...prevInput,
                [name]: value
            }
        })
    }

    function expand() {
        setExpanded(true);
    }

    return (
        <form className="create-note" onSubmit={(event) => event.preventDefault()}>
            {isExpanded && <input onChange={handleChange} name="title" placeholder="Title" value={input.title} />}
            <textarea onClick={expand} onChange={handleChange} name="content" placeholder="Take a note..." value={input.content} rows={isExpanded ? 4 : 1} />
            <Zoom in={isExpanded}>
                <Fab color="primary" onClick={() => onAdd(input, setInput)}>
                    <AddIcon />
                </Fab>
            </Zoom>
        </form>
    )
}

export default CreateArea;